export default {
	props: {
		size: {
			type: String,
			default: null
		}
	},
	computed: {
		sizeFormClass: function sizeFormClass() {
			return [this.size ? 'form-control-' + this.size : null];
		},
		sizeBtnClass: function sizeBtnClass() {
			return [this.size ? 'btn-' + this.size : null];
		}
	}
};